import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useState } from 'react';
import axios from 'axios';

const defaultTheme = createTheme();

export default function SignIn({ setPage }) {
  const [errorMessage, setErrorMessage] = useState('');
  const [username, setUsername] = useState('');
  const [loginSuccess, setLoginSuccess] = useState(false);

  const handleSubmit = async (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    const email = data.get('email');
    const password = data.get('password');

    if (email === '2200032891cseh@gmail.com' && password === 'Harsha') {
      setPage('AdminPanel');
      setErrorMessage('Admin recognized!');
    } else {
      try {
        const response = await axios.post('http://localhost:8080/login', { email, password });
        if (response.status === 200) {
          setUsername(response.data.username);
          setLoginSuccess(true);
          setTimeout(() => setPage('City'), 1000);
        }
      } catch (error) {
        if (error.response && error.response.status === 401) {
          setErrorMessage('Invalid credentials. Please try again.');
        } else {
          setErrorMessage('An error occurred during login. Please try again.');
        }
      }
    }
  };

  return (
    <ThemeProvider theme={defaultTheme}>
      <style>
        {`
          @keyframes fadeIn {
            from { opacity: 0; transform: translateY(-10px); }
            to { opacity: 1; transform: translateY(0); }
          }

          @keyframes slideDown {
            from { opacity: 0; transform: translateY(-20px); }
            to { opacity: 1; transform: translateY(0); }
          }

          .animatedBox {
            animation: fadeIn 1s ease-in-out;
          }

          .successMessage {
            animation: slideDown 0.5s ease-in-out;
          }

          .hoverEffect:hover {
            transform: scale(1.05);
            transition: transform 0.3s ease-in-out;
          }
        `}
      </style>

      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          className="animatedBox"
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            backgroundColor: '#e8eaf6', // Light background similar to SignUp
            borderRadius: 3,
            padding: 4,
            boxShadow: 4,
            minHeight: '70vh',
            justifyContent: 'center',
            color: '#000',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: '#ff5252' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5" sx={{ fontWeight: 700, mb: 1, color: '#283593' }}>
            Sign in
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1, width: '100%' }}>
            {errorMessage && (
              <Typography color="error" sx={{ textAlign: 'center', mb: 2 }}>
                {errorMessage}
              </Typography>
            )}
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              autoFocus
              InputLabelProps={{ style: { color: '#283593' } }}
              sx={{
                input: { color: '#283593' },
                '& .MuiOutlinedInput-root': {
                  borderRadius: 2,
                  '& fieldset': { borderColor: '#283593' },
                  '&:hover fieldset': { borderColor: '#5c6bc0' },
                },
              }}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              InputLabelProps={{ style: { color: '#283593' } }}
              sx={{
                input: { color: '#283593' },
                '& .MuiOutlinedInput-root': {
                  borderRadius: 2,
                  '& fieldset': { borderColor: '#283593' },
                  '&:hover fieldset': { borderColor: '#5c6bc0' },
                },
              }}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              className="hoverEffect"
              sx={{
                mt: 3,
                mb: 2,
                bgcolor: '#ff5252',
                '&:hover': { bgcolor: '#ff867f' },
                borderRadius: 3,
                paddingY: 1.2,
              }}
            >
              Sign In
            </Button>
            <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2" sx={{ color: '#283593' }}>
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
                <Link
                  component="button"
                  variant="body2"
                  onClick={() => setPage('Signup')}
                  sx={{ color: '#283593', fontWeight: 'bold' }}
                >
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid>
          </Box>
          {loginSuccess && (
            <Typography variant="h6" className="successMessage" sx={{ mt: 3, textAlign: 'center', color: '#4caf50', fontWeight: 'bold' }}>
              Welcome back, {username}!
            </Typography>
          )}
        </Box>
      </Container>
    </ThemeProvider>
  );
}
