import './App.css';
import SignUp from './components/SignUp';
import SignIn from './components/SignIn';
import Appbar from './components/Appbar';
import StudentAppBar from './components/StudentAppBar'; // Import the new AppBar
import User from './components/User';
import Show from './components/Show';
import Home from './components/Home';
import { useState } from 'react';
import City from './components/City';
import About from './components/About';
import AdminPanel from './components/AdminPanel';
import AdminAppBar from './components/AdminAppBar';

function App() {
  const [page, setPage] = useState("Home");

  function Page() {
    switch (page) {
      case "Signin":
        return (
          <div>
            <Appbar setPage={setPage} />
            <SignIn setPage={setPage} />
          </div>
        );
      case "Signup":
        return (
          <div>
            <Appbar setPage={setPage} />
            <SignUp setPage={setPage} />
          </div>
        );
      case "User":
        return (
          <div>
            <Appbar setPage={setPage} />
            <User setPage={setPage} />
          </div>
        );
      case "Show":
        return (
          <div>
            <Appbar setPage={setPage} />
            <Show setPage={setPage} />
          </div>
        );
      case "City":
        return (
          <div>
            <StudentAppBar setPage={setPage} /> {/* Use Student AppBar here */}
            <City setPage={setPage} />
          </div>
        );

      case "About":
        return (
          <div>
            <StudentAppBar setPage={setPage} /> {/* Use Student AppBar here */}
            <About setPage={setPage} />
          </div>
        );
        case "AdminPanel":
  return (
    <div>
      <AdminAppBar setPage={setPage} />
      <AdminPanel setPage={setPage} />
    </div>
  );

      case "AdminPanel":
        return (
          <div>
            <Appbar setPage={setPage} />
            <AdminPanel setPage={setPage} />
          </div>
        );
      default:
        return (
          <div>
            <Appbar setPage={setPage} />
            <Home setPage={setPage} />
          </div>
        );
    }
  }

  return (
    <div className="App">
      <div className='App-body'>
        <Page />
      </div>
    </div>
  );
}

export default App;
