import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import axios from 'axios';
import { useState } from 'react';

const defaultTheme = createTheme();

export default function SignUp({ setPage }) {
  const [userName, setUserName] = useState('');
  const [signUpSuccess, setSignUpSuccess] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    axios
      .post('http://localhost:8080/user', {
        name: data.get('name'),
        email: data.get('email'),
        password: data.get('password'),
      })
      .then((res) => {
        setUserName(data.get('name'));
        setSignUpSuccess(true);
        setTimeout(() => {
          setPage('Signin');
        }, 1000);
      })
      .catch((error) => {
        console.error('Error submitting form!', error);
      });
  };

  return (
    <ThemeProvider theme={defaultTheme}>
      <style>
        {`
          @keyframes fadeIn {
            from { opacity: 0; transform: translateY(-10px); }
            to { opacity: 1; transform: translateY(0); }
          }
          
          @keyframes slideDown {
            from { opacity: 0; transform: translateY(-20px); }
            to { opacity: 1; transform: translateY(0); }
          }

          .animatedBox {
            animation: fadeIn 1s ease-in-out;
          }

          .successMessage {
            animation: slideDown 0.5s ease-in-out;
          }

          .hoverEffect:hover {
            transform: scale(1.05);
            transition: transform 0.3s ease-in-out;
          }
        `}
      </style>

      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          className="animatedBox"
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            background: 'linear-gradient(135deg, #e8eaf6, #7986cb)', // Light blue gradient
            borderRadius: 3,
            padding: 4,
            boxShadow: 4,
            minHeight: '70vh',
            justifyContent: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: '#ff5252' }}> {/* Soft coral color */}
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5" sx={{ fontWeight: 700, mb: 1, color: '#283593' }}> {/* Dark blue for contrast */}
            Student Sign Up
          </Typography>
          <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 2, width: '100%' }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  autoComplete="given-name"
                  name="name"
                  required
                  fullWidth
                  id="name"
                  label="Name"
                  autoFocus
                  InputLabelProps={{ style: { color: '#283593' } }}
                  sx={{
                    input: { color: '#283593' },
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 2,
                      '& fieldset': { borderColor: '#283593' },
                      '&:hover fieldset': { borderColor: '#5c6bc0' },
                    },
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  autoComplete="email"
                  InputLabelProps={{ style: { color: '#283593' } }}
                  sx={{
                    input: { color: '#283593' },
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 2,
                      '& fieldset': { borderColor: '#283593' },
                      '&:hover fieldset': { borderColor: '#5c6bc0' },
                    },
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                  InputLabelProps={{ style: { color: '#283593' } }}
                  sx={{
                    input: { color: '#283593' },
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 2,
                      '& fieldset': { borderColor: '#283593' },
                      '&:hover fieldset': { borderColor: '#5c6bc0' },
                    },
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <FormControlLabel
                  control={<Checkbox value="allowExtraEmails" color="primary" />}
                  label="I want to receive inspiration, marketing promotions, and updates via email."
                  sx={{ color: '#283593' }}
                />
              </Grid>
            </Grid>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              className="hoverEffect"
              sx={{
                mt: 3,
                mb: 2,
                bgcolor: '#ff5252', // Coral color for button
                '&:hover': { bgcolor: '#ff867f' }, // Lighter coral on hover
                borderRadius: 3,
                paddingY: 1.2,
              }}
            >
              Sign Up
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item>
                <Link
                  href="#"
                  variant="body2"
                  onClick={() => setPage('Signin')}
                  sx={{ color: '#283593', fontWeight: 'bold' }}
                >
                  Already have an account? Sign in
                </Link>
              </Grid>
            </Grid>
          </Box>

          {signUpSuccess && (
            <Typography variant="h6" className="successMessage" sx={{ mt: 3, textAlign: 'center', color: '#4caf50', fontWeight: 'bold' }}> {/* Success green */}
              Welcome, {userName}! You will be redirected to the Sign In page shortly.
            </Typography>
          )}
        </Box>
      </Container>
    </ThemeProvider>
  );
}
