import React, { useEffect, useState } from 'react';
import axios from 'axios';

const AdminPanel = () => {
    const [users, setUsers] = useState([]);
    const [events, setEvents] = useState([]);
    const [newEvent, setNewEvent] = useState({ name: '', description: '' });
    
    // Fetch users and events on component mount
    useEffect(() => {
        fetchUsers();
        fetchEvents();
    }, []);

    const fetchUsers = async () => {
        try {
            const response = await axios.get('/api/admin/users');
            setUsers(response.data);
        } catch (error) {
            console.error('Error fetching users', error);
        }
    };

    const fetchEvents = async () => {
        try {
            const response = await axios.get('/api/admin/events');
            setEvents(response.data);
        } catch (error) {
            console.error('Error fetching events', error);
        }
    };

    const handleEventChange = (e) => {
        const { name, value } = e.target;
        setNewEvent({ ...newEvent, [name]: value });
    };

    const handleAddEvent = async (e) => {
        e.preventDefault();
        try {
            await axios.post('/api/admin/events', newEvent);
            fetchEvents(); // Refresh the event list
            setNewEvent({ name: '', description: '' }); // Reset form
        } catch (error) {
            console.error('Error adding event', error);
        }
    };

    const handleDeleteEvent = async (id) => {
        try {
            await axios.delete(`/api/admin/events/${id}`);
            fetchEvents(); // Refresh the event list
        } catch (error) {
            console.error('Error deleting event', error);
        }
    };

    return (
        <div className="admin-panel">
            <h1>Admin Panel</h1>
            <div className="users-section">
                <h2>Registered Users</h2>
                <ul>
                    {users.map(user => (
                        <li key={user.email}>{user.name} ({user.email})</li>
                    ))}
                </ul>
            </div>

            <div className="events-section">
                <h2>Manage Events</h2>
                <form onSubmit={handleAddEvent}>
                    <input
                        type="text"
                        name="name"
                        placeholder="Event Name"
                        value={newEvent.name}
                        onChange={handleEventChange}
                        required
                    />
                    <input
                        type="text"
                        name="description"
                        placeholder="Event Description"
                        value={newEvent.description}
                        onChange={handleEventChange}
                        required
                    />
                    <button type="submit">Add Event</button>
                </form>

                <ul>
                    {events.map(event => (
                        <li key={event.id}>
                            {event.name} - {event.description}
                            <button onClick={() => handleDeleteEvent(event.id)}>Delete</button>
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    );
};

export default AdminPanel;
